.app-header {
  align-items: flex-end;
}

.app-header h1 {
  flex-grow: 1;
}

.app-header h2 {
  font-size: 1.2rem;
  color: grey;
}
.todo-list-item {
  font-size: 1.25rem;
}

.todo-list-item button {
  width: 35px;
  margin: 3px;
}

.todo-list-item-label {
  margin-left: 1.25rem;
  line-height: 35px;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.todo-list-item.done .todo-list-item-label {
  text-decoration: line-through;
}

.todo-list-item.important .todo-list-item-label {
  font-weight: bold;
  color: steelblue;
}

.todo-list .list-group-item {
  padding: .25rem .75rem;
}

.search-panel {
  margin: 1rem 0;
}

.search-panel .search-input {
  width: auto;
  flex-grow: 1;
  margin-right: 3px;
}

.bottom-panel {
  margin-top: 10px;
}

.bottom-panel .new-todo-label {
  width: auto;
  flex-grow: 1;
  margin-right: 3px;
}

.login-form {
  position: fixed;
  display: flex;
  flex-direction: column;
  padding: 30px 50px;
  min-width: 500px;
  min-height: 200px;
  top: 100px;
  background-color: white;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  z-index: 2;
}

.form-title {
  text-align: center;
  margin-bottom: 20px;
}

.form-field {
  display: flex;
  margin-bottom: 20px;
}

.buttons-field {
  display: flex;
  justify-content: space-around;
}

.form-label {
  margin-top: 5px;
  margin-right: 10px;
  width: 100px;
}

.hidden {
  display: none;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.hidden {
  display: none;
}

.todo-app {
  margin: 2rem auto 0 auto;
  max-width: 500px;
}

